import React, { useState, useEffect } from "react";
import { inject, observer } from "mobx-react";
import classnames from "classnames";
import DeriNumberFormat from "../../utils/DeriNumberFormat";
import "./leaderboard.less";
import Icon from "../../components/Icon/Icon";
import { gql } from "graphql-request";
import { graph_request_test } from "../../utils/graph_api";
import { formatAddress } from "../../utils/utils";
import { useCallback } from "react";
import { useWallet } from "use-wallet";

function Leaderboard() {
  const [rankingsList, setRankingsList] = useState<any>([]);
  const [accountDays, setAccountDays] = useState(30);
  const [listDays, setListDays] = useState(30);
  const [accountInfo, setAccountInfo] = useState<any>({});
  const {  account } = useWallet();
  const timeDay: number = 86400;
  const loadList = useCallback(async () => {
    let query = gql`
      query ($chainId: Int!, $fromTimestamp: Int!) {
        tradePnl(where: { chainId: $chainId, fromTimestamp: $fromTimestamp }) {
          account
          chainId
          liquidationLoss
          notional
          pnl
          tradeFee
          tradePnl
        }
      }
    `;
    let timestamp = 0;
    let date = new Date();
    let now = date.getTime() / 1000;
    if (listDays === 7) {
      timestamp = now - timeDay * 7;
    }
    if (listDays === 30) {
      timestamp = now - timeDay * 30;
    }
    let variables = { chainId: 42161, fromTimestamp: parseInt(String(timestamp)) };
    let data = await graph_request_test(query, variables);
    if (data.tradePnl.length) {
      let list = data.tradePnl;
      list = list.slice(0,50)
      setRankingsList(list);
    }
  }, [listDays]);
  const getAccountInfo = useCallback(async () => {
    let query = gql`
      query ($chainId: Int!, $fromTimestamp: Int!, $account: String) {
        tradePnl(
          where: {
            account: $account
            chainId: $chainId
            fromTimestamp: $fromTimestamp
          }
        ) {
          account
          chainId
          liquidationLoss
          notional
          pnl
          tradeFee
          tradePnl
        }
      }
    `;
    let timestamp = 0;
    let date = new Date();
    let now = date.getTime() / 1000;
    if (accountDays === 7) {
      timestamp = now - timeDay * 7;
    }
    if (accountDays === 30) {
      timestamp = now - timeDay * 30;
    }
    let variables = {
      account: account,
      chainId: 56,
      fromTimestamp: parseInt(String(timestamp)),
    };
    let data = await graph_request_test(query, variables);
    if (data.tradePnl.length) {
      let list = data.tradePnl[0];
      setAccountInfo(list);
    }else{
      setAccountInfo({
        tradeFee:0,
        pnl:0,
        notional:0
      })
    }
  }, [accountDays, account]);
  useEffect(() => {
    loadList();
  }, [listDays]);
  useEffect(() => {
    if (account) {
      getAccountInfo();
    }
  }, [account, accountDays]);
  return (
    <div className="Leaderboard">
      <div className="Leaderboard-box">
        <div className="my-historical-trading">
          <div className="title-tab-day">
            <div className="title-leaderboard">
              My Historical Trading Performance
            </div>
            <div className="tab-day">
              <div
                className={classnames("day-btn", {
                  active: accountDays === 7,
                })}
                onClick={() => {
                  setAccountDays(7);
                }}
              >
                7 DAYS
              </div>
              <div
                className={classnames("day-btn", {
                  active: accountDays === 30,
                })}
                onClick={() => {
                  setAccountDays(30);
                }}
              >
                30 DAYS
              </div>
            </div>
          </div>
          <div className="my-info-list">
            <div className="info-box">
              <div className="info-num">
                {" "}
                <DeriNumberFormat
                  prefix="$ "
                  value={accountInfo.notional}
                  decimalScale={2}
                />
              </div>
              <div className="info-title"> Notional </div>
            </div>
            <div className="info-box">
              <div className="info-num">
                <DeriNumberFormat
                  prefix="$ "
                  value={+accountInfo.tradeFee}
                  decimalScale={2}
                />
              </div>
              <div className="info-title"> Transaction Fee </div>
            </div>
            <div className="info-box">
              <div className="info-num">
                {" "}
                <DeriNumberFormat
                  prefix="$ "
                  value={+accountInfo.pnl}
                  decimalScale={2}
                />
              </div>
              <div className="info-title"> PNL </div>
            </div>
          </div>
        </div>
        <div className="rankings">
          <div className="rankings-title-tab-day">
            <div className="rankings-title">Rankings</div>
            <div className="tab-day">
              <div
                className={classnames("day-btn", { active: listDays === 7 })}
                onClick={() => {
                  setListDays(7);
                }}
              >
                7 DAYS
              </div>
              <div
                className={classnames("day-btn", { active: listDays === 30 })}
                onClick={() => {
                  setListDays(30);
                }}
              >
                30 DAYS
              </div>
            </div>
          </div>
          <div className="rankings-list-title">
            <div className="rank">Rank</div>
            <div className="trade">Trade</div>
            <div className="notional">Notional</div>
            <div className="transaction-fee">Transaction Fee</div>
            <div className="pnl">PNL</div>
          </div>
          <div className="rankings-list-num">
            {rankingsList.map((item, index) => {
              return (
                <div className="rankings-list-num-box" key={index}>
                  <div className="rank">
                    {index + 1 === 1 && (
                      <Icon
                        token="topOne"
                        width={undefined}
                        height={undefined}
                        secondary={undefined}
                        className={undefined}
                        onClick={undefined}
                      />
                    )}
                    {index + 1 === 2 && (
                      <Icon
                        token="topTwo"
                        width={undefined}
                        height={undefined}
                        secondary={undefined}
                        className={undefined}
                        onClick={undefined}
                      />
                    )}
                    {index + 1 === 3 && (
                      <Icon
                        token="topThree"
                        width={undefined}
                        height={undefined}
                        secondary={undefined}
                        className={undefined}
                        onClick={undefined}
                      />
                    )}
                    {index + 1 > 3 && (
                      <span className="top-num">{index + 1}</span>
                    )}
                  </div>
                  <div className="trade">{formatAddress(item.account)}</div>
                  <div className="notional">
                    ${" "}
                    <DeriNumberFormat
                      value={item.notional}
                      displayType="text"
                      decimalScale={2}
                    />
                  </div>
                  <div className="transaction-fee">
                    ${" "}
                    <DeriNumberFormat
                      value={+item.tradeFee}
                      displayType="text"
                      decimalScale={2}
                    />
                  </div>
                  <div
                    className={classnames("pnl", {
                      up: +item.pnl > 0,
                      down: +item.pnl < 0,
                    })}
                  >
                    {+item.pnl > 0 ? "+" : ""}
                    <DeriNumberFormat
                      value={+item.pnl}
                      displayType="text"
                      decimalScale={2}
                      prefix="$ "
                      // allowZero={true}
                    />
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
}
export default inject("wallet")(observer(Leaderboard));
